.download-box{
  width: 100%;
  height: 500px;
  font-family: Korolev,sans-serif;
  .title{
    font-family: Tachyon,sans-serif;
    font-weight: 300;
    color: #d4e05c;
    font-size: 24px;
    letter-spacing: 2px;
    margin-bottom: 50px;
  }
  
  .img-down{
    display: flex;
    justify-content: space-between;
    .down{
      margin-left: 20px;
      
      a{
        display: block;
        width: 100px;
        height: 40px;
        text-align: center;
        line-height: 40px;
        color: #fff;
        margin-top: 40px;
        background-color: #2c2d31;
        border-radius: 10px;
      }
    }
  }
}